* {
  display: flexbox;
  font-family: sans-serif;
  text-align: center;
}
body {
  background: #fff;
}

.result {
  background-color: black;
  padding: 8;
  color: whitesmoke;
  font-weight: 600;
  text-align: center;
  margin-top: 3px;
  border-radius: 3px;
  height: 70px;
  width: 100%;
}

.calc {
  max-width: 300px;
  margin: auto;
}

.button {
  display: block;
  background-color: #000;
}

button {
  width: 25%;
  height: 60px;
  font-size: 30px;
  border-radius: 3px;
  background-image: linear-gradient(
    to right top,
    #d16ba5,
    #c777b9,
    #ba83ca,
    #aa8fd8,
    #9a9ae1,
    #8aa7ec,
    #79b3f4,
    #69bff8,
    #52cffe,
    #41dfff,
    #46eefa,
    #5ffbf1
  );
}
